import React, { useEffect, useState } from "react";
import PopupWrapper from "../../../../MembersTab/staff/Mainbody/FinanceManager/FeesStructure/PopupWrapper";
import style from "../../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/MiddleContent/Funds/Funds.module.css";
import QLoader from "../../../../Loader/QLoader";
import { useTranslation } from "react-i18next";
import { CSVLink } from "react-csv";
import BorderBottom from "../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useExportStudentAttendanceZip } from "../DepartmentApi/depart-api";
import useCalendar from "../../../../MembersTab/staff/Mainbody/FinanceManager/Payroll/Employee/useCalender";
import tileStyle from "./Tile.module.css";
import JoinFormCalender from "../../../../JoiningForm/Student/Form/JoinFormCalender";

const ExportStudentAttendance = ({ onClose, cid, className }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [filled, setFilled] = useState(false);
  const [downloadLink, setDownloadLink] = useState(false);
  const [exportData, setExportData] = useState([]);
  const [isType, setIsType] = useState("");
  const [range, setRange] = useState({
    startRange: "",
    endRange: "",
  });

  const { daysInMonth, selectedDate, monthNames, getNextMonth, getPrevMonth } =
    useCalendar();
  const [exportStudentAttendanceZip] = useExportStudentAttendanceZip();

  useEffect(() => {
    if (downloadLink) {
      let downlabel = document.getElementById("attendance_download_file");
      downlabel.click();
      setDownloadLink(false);
      setExportData([]);
      setDisabled((pre) => !pre);
    }
  }, [downloadLink]);
  const onNext = () => {
    getNextMonth();
  };
  const onPrev = () => {
    getPrevMonth();
  };
  const generateMonthWiseExcel = (list) => {
    if (isType === "ALL") {
      let obj = {
        GRNO: "",
        "Enrollment / PRN": "",
        RollNo: "",
        Name: "",
        Gender: "",
        [list.className]: "",
        "Class Overall Percentage": "",
      };

      for (let sub of list?.mapSubject) {
        obj[sub.subjectName] = "";
        obj[`${sub.subjectName} Overall Percentage`] = "";
      }

      obj["Overall Percentage"] = "";
      const student = list?.students?.map((stu) => {
        let over_sum = 0;
        let dObj = { ...obj };
        dObj.GRNO = stu?.studentGRNO ?? "N/A";
        dObj["Enrollment / PRN"] = stu?.student_prn_enroll_number
          ? stu?.student_prn_enroll_number
          : "N/A";
        dObj.RollNo = stu?.studentROLLNO;
        dObj.Name = `${
          stu?.studentFirstName +
          " " +
          stu?.studentMiddleName +
          " " +
          stu?.studentLastName
        }`;
        dObj.Gender = stu?.studentGender;
        dObj[
          stu.className
        ] = `${stu.classWise.presentCount} out of ${stu.classWise.totalCount}`;
        dObj["Class Overall Percentage"] = stu.classWise.totalPercentage;
        for (let sub of stu?.subjects) {
          dObj[
            sub.subjectName
          ] = `${sub.presentCount} out of ${sub.totalCount}`;
          dObj[`${sub.subjectName} Overall Percentage`] = sub.totalPercentage;
          if (sub.totalPercentage) over_sum += sub.totalPercentage;
        }
        dObj["Overall Percentage"] = (over_sum / stu?.subjects?.length).toFixed(
          2
        );
        return dObj;
      });
      return student;
    } else if (isType === "ALL_SUBJECT_SEMESTER") {
      let obj = {
        GRNO: "",
        "Enrollment / PRN": "",
        RollNo: "",
        Name: "",
        Gender: "",
      };

      for (let sub of list?.mapSubject) {
        obj[sub.subjectName] = "";
        obj[`${sub.subjectName} Overall Percentage`] = "";
      }
      const student = list?.students?.map((stu) => {
        let dObj = { ...obj };
        dObj.GRNO = stu?.studentGRNO ?? "N/A";
        dObj["Enrollment / PRN"] = stu?.student_prn_enroll_number
          ? stu?.student_prn_enroll_number
          : "N/A";
        dObj.RollNo = stu?.studentROLLNO;
        dObj.Name = `${
          stu?.studentFirstName +
          " " +
          stu?.studentMiddleName +
          " " +
          stu?.studentLastName
        }`;
        dObj.Gender = stu?.studentGender;
        for (let sub of stu?.subjects) {
          dObj[
            sub.subjectName
          ] = `${sub.presentCount} out of ${sub.totalCount}`;
          dObj[`${sub.subjectName} Overall Percentage`] = sub.totalPercentage;
        }
        return dObj;
      });
      return student;
    } else if (isType === "ALL_SUBJECT_MONTHLY") {
      let obj = {
        GRNO: "",
        "Enrollment / PRN": "",
        RollNo: "",
        Name: "",
        Gender: "",
      };

      for (let sub of list?.mapSubject) {
        obj[sub.subjectName] = "";
        obj[`${sub.subjectName} Overall Percentage`] = "";
      }
      const student = list?.students?.map((stu) => {
        let dObj = { ...obj };
        dObj.GRNO = stu?.studentGRNO ?? "N/A";
        dObj["Enrollment / PRN"] = stu?.student_prn_enroll_number
          ? stu?.student_prn_enroll_number
          : "N/A";
        dObj.RollNo = stu?.studentROLLNO;
        dObj.Name = `${
          stu?.studentFirstName +
          " " +
          stu?.studentMiddleName +
          " " +
          stu?.studentLastName
        }`;
        dObj.Gender = stu?.studentGender;
        for (let sub of stu?.subjects) {
          dObj[
            sub.subjectName
          ] = `${sub.presentCount} out of ${sub.totalCount}`;
          dObj[`${sub.subjectName} Overall Percentage`] = sub.totalPercentage;
        }
        return dObj;
      });
      return student;
    } else {
      let newDate = new Date(
        `${daysInMonth} ${
          monthNames[selectedDate.getMonth()]
        } ${selectedDate.getFullYear()}`
      );
      newDate = newDate.toISOString();
      const student = list?.students?.map((stu) => {
        let dObj = {};
        // let dObj = {...OneExcel};
        if (isType === "RANGE") {
          for (let i = 1; i <= daysInMonth; i++) {
            dObj[`${i}/${newDate.substring(5, 7)}/${newDate.substring(0, 4)}`] =
              "";
          }
        } else {
          for (let i = 1; i <= daysInMonth; i++) {
            if (i < 10) {
              let db = `0${i}/${newDate.substring(5, 7)}/${newDate.substring(
                0,
                4
              )}`;
              if (list?.attendaceMappingDate?.includes(db)) dObj[db] = "";
            } else {
              let dbt = `${i}/${newDate.substring(5, 7)}/${newDate.substring(
                0,
                4
              )}`;
              if (list?.attendaceMappingDate?.includes(dbt)) dObj[dbt] = "";
            }
          }
        }
        for (let avail of stu?.availablity) {
          // if (+i === +avail?.date?.substring(0, 2)) {
          // dObj[`${+avail?.date?.substring(0, 2)}`] = avail?.status;
          dObj[`${avail?.date}`] = avail?.status;
          // break;
          // }
        }
        let obj = {
          GRNO: stu?.studentGRNO ?? "N/A",
          "Enrollment / PRN": stu?.student_prn_enroll_number
            ? stu?.student_prn_enroll_number
            : "N/A",
          RollNo: stu?.studentROLLNO,
          Name:
            stu?.studentFirstName +
            " " +
            stu?.studentMiddleName +
            " " +
            stu?.studentLastName,
          Gender: stu?.studentGender,
          ...dObj,
          "Total Count": `${stu?.classWise?.presentCount} out of ${stu?.classWise?.totalCount}`,
          "Overall Percentage": stu?.classWise?.totalPercentage,
        };
        return obj;
      });
      return student;
    }
  };

  const onType = (val) => {
    setIsType(val);
  };
  const onValidation = (data) => {
    const errors = {};
    for (let obj in data) {
      if (!data[obj]) errors[obj] = `${obj} is required!`;
    }
    return errors;
  };

  const onExportData = () => {
    let flag = false;
    let validation = "";
    if (isType === "RANGE") {
      validation = onValidation(range);
      for (let errField in validation) {
        if (errField) {
          flag = true;
          break;
        }
      }
    } else {
      flag = false;
    }
    if (flag) {
      setFilled(validation);
    } else {
      setDisabled((pre) => !pre);
      let newDate = new Date(
        `${daysInMonth} ${
          monthNames[selectedDate.getMonth()]
        } ${selectedDate.getFullYear()}`
      );
      newDate = newDate.toISOString();
      exportStudentAttendanceZip({
        cid: cid,
        is_type: isType,
        month: `${newDate.substring(5, 7)}`,
        year: `${newDate.substring(0, 4)}`,
        zipData: range,
      })
        .then(async (res) => {
          let data = await generateMonthWiseExcel(res.data?.attendance_zip);
          setExportData(() => data);
          setDownloadLink(true);
        })
        .catch(() => {});
    }
  };

  // const onDateFunctionFrom = (val) => {
  //   setRange((prev) => ({
  //     ...prev,
  //     startRange: val,
  //   }));
  // };
  // const onDateFunctionTo = (val) => {
  //   setRange((prev) => ({
  //     ...prev,
  //     endRange: val,
  //   }));
  // };
  return (
    <PopupWrapper onClose={onClose}>
      <div
        className={style.filter_container}
        style={{
          minHeight: "15rem",
          width: "40rem",
        }}
      >
        <h6 className={style.filter_title}>
          {t("filter_and_export_student_attendance")}
        </h6>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />

        <section
          className={tileStyle.tile_tile_container}
          style={{
            marginTop: "1rem",
          }}
        >
          <div
            className={
              isType === "" ? tileStyle.tile_tile_active : tileStyle.tile_tile
            }
            onClick={() => onType("")}
          >
            {t("monthly")}
          </div>
          <div
            className={
              isType === "ALL_SUBJECT_MONTHLY"
                ? tileStyle.tile_tile_active
                : tileStyle.tile_tile
            }
            onClick={() => onType("ALL_SUBJECT_MONTHLY")}
          >
            {t("all_subject_monthly")}
          </div>
          {/* <div
            className={
              isType === "RANGE"
                ? tileStyle.tile_tile_active
                : tileStyle.tile_tile
            }
            onClick={() => onType("RANGE")}
          >
            {t("date_range")}
          </div> */}
          <div
            className={
              isType === "ALL"
                ? tileStyle.tile_tile_active
                : tileStyle.tile_tile
            }
            onClick={() => onType("ALL")}
          >
            {t("semester")}
          </div>
          <div
            className={
              isType === "ALL_SUBJECT_SEMESTER"
                ? tileStyle.tile_tile_active
                : tileStyle.tile_tile
            }
            onClick={() => onType("ALL_SUBJECT_SEMESTER")}
          >
            {t("all_subject_semester")}
          </div>
        </section>
        {!isType || isType === "ALL_SUBJECT_MONTHLY" ? (
          <section className={style.fees_head_title}>
            <img
              src="/images/members/back-icon-white.svg"
              style={{
                cursor: "pointer",
              }}
              onClick={onPrev}
            />

            <h6 className={`${style.yearmonth}`}>
              {`${monthNames[selectedDate.getMonth()]}`}{" "}
              {`${selectedDate.getFullYear()}`}
            </h6>

            <img
              src="/images/members/forward-icon-white.svg"
              style={{
                cursor: "pointer",
              }}
              onClick={onNext}
            />
          </section>
        ) : null}

        <button
          className={style.next_btn}
          onClick={onExportData}
          style={{
            marginTop: "3rem",
          }}
        >
          {t("export_data")}
        </button>
        <CSVLink
          data={exportData}
          filename={`${className}-${
            isType === "ALL"
              ? "semester"
              : isType === "ALL_SUBJECT_SEMESTER"
              ? "all-subject-semester"
              : isType === "ALL_SUBJECT_MONTHLY"
              ? `all-subject-${monthNames[selectedDate.getMonth()]}-monthly`
              : `${monthNames[selectedDate.getMonth()]}-monthly`
          }-attendance.csv`}
          id="attendance_download_file"
        />
      </div>
      {disabled && <QLoader />}
    </PopupWrapper>
  );
};

export default ExportStudentAttendance;
